body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.hex-map-row-super-container {
  display: flex;
}
.hexagon-box{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hexagon {
  background: #000;
  -webkit-clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  opacity: 0.8;
  -webkit-transform: rotate(90deg) scale(1.05);
          transform: rotate(90deg) scale(1.05);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.visible{
  background: green;
}
.user-visible{
  background-color: #fffa;
}
.hexagon-box:hover .hexagon.visible{
  background: #fff;
}
.hexagon-box:hover .hexagon.user-visible{
  background: #fff;
}
.hexagon-none{
  background: #000
}
.player{
  width: 60px;
  height: 60px;
  border-radius: 50%;
  position: absolute;
}
.kek-counter{
  position: absolute;
}
.ui-map-container {
    zoom: 0.5;    
    padding: 16px;
    padding-top: 200px;
}
.UIMain-container {
    background-color: #22282f;
}
.UIBottom-container {
    position: fixed;
    bottom: 0px;
    left: 0px;
}
.UIBottom-person {
    background: white;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}
.UIBottom-person-tools {
    background: white;
    width: 100px;
    height: 100px;
}
.UIBottom-other-persons {
    position: absolute;
    height: 100px;
    width: 200px;
    background: red;
    bottom: 0px;
    left: 100px;
}
.UIHeader-container {
    position: fixed;
    right: 0;
    top: 0;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
img { 
  image-rendering: optimizeSpeed;             /* STOP SMOOTHING, GIVE ME SPEED  */
  image-rendering: -moz-crisp-edges;          /* Firefox                        */
  image-rendering: -o-crisp-edges;            /* Opera                          */ /* Chrome (and eventually Safari) */
  image-rendering: pixelated; /* Chrome */
  image-rendering: optimize-contrast;         /* CSS3 Proposed                  */
  -ms-interpolation-mode: nearest-neighbor;   /* IE8+                           */
}
p { 
    font-family: '8BITWONDERNominal'; 
    font-weight: normal; 
    font-style: normal; 
}

body {
  font-family: '8BITWONDERNominal'; 
  font-weight: normal; 
  font-style: normal; 
}
.room-container {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.room-name-input {
    margin-bottom: 20px;
}
.room-window {
    width: 600px;
    height: 500px;
    background-color: #52637d;
    display: flex;
    background-color: #52637d;
    justify-content: center;
    align-items: center;
}
